@font-face {
    font-family: 'proza_libre';
    src: url('/assets/fonts/prozalibre-bold-webfont.woff2') format('woff2'),
         url('/assets/fonts/prozalibre-bold-webfont.woff') format('woff');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'proza_libre';
    src: url('/assets/fonts/prozalibre-regular-webfont.woff2') format('woff2'),
         url('/assets/fonts/prozalibre-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'proza_libre';
    src: url('/assets/fonts/prozalibre-italic-webfont.woff2') format('woff2'),
         url('/assets/fonts/prozalibre-italic-webfont.woff') format('woff');
    font-weight: normal;
    font-style: italic;
}

:root {
    --color-2: #2F2E2C;
    --color-3: #9c596a;

    --font-stack-heading: "proza_libre", var(--font-stack-body);
}

html {
    font-family: "proza_libre", var(--font-stack-body);
}

header, main {
    max-width: 80%;
}

main {
    p {
        max-width: 35em;
        margin-left: auto;
        margin-right: auto;
    }
}

header {
    margin: 2em auto 0;
    text-align: center;

    p {
        text-transform: uppercase;
        letter-spacing: 0.2em;

        color: var(--color-3);
        font-weight: bold;
    }
}

h1 {
    font-size: 3em;
    margin-top: 2rem;
    line-height: 1;
    text-transform: none;
    font-weight: normal;
}

section + section {
    margin-top: 2em;
}

img {
    margin-left: auto;
    margin-right: auto;
    max-width: 35em;
}

figcaption {
    margin-top: 2em;
}

#info {
    font-style: italic;
    p + p {
        margin-top: 0;
    }
}

#palette img {
    margin: 2em auto;
    max-width: 50%;
}

figure {
    max-width: 35em;
}